import {IEmpJobDataModel, IEmpJobDataRequest,} from "app/personnel-setup/models/employee-employment-info";
import {Component, EventEmitter, OnInit, Output} from "@angular/core";
import {MatTableDataSource} from "@angular/material/table";
import {AppHelpers} from "app/shared/resources/app-helpers";
import {FormGroup, Validators} from "@angular/forms";
import {IResponse} from "app/shared/interfaces/responces";
import {MessageTypes} from "app/shared/enums/message-types.enum";
import {
  EmployeeJobInformationService
} from "app/personnel-setup/services/employees/employement-information/employee-job-information.service";
import {EmpGeneralInfoService} from "app/personnel-setup/services/employees/general-info/emp-general-info.service";

@Component({
  selector: "emp-job-section",
  templateUrl: "./emp-job-section.component.html",
  styleUrls: ["./emp-job-section.component.scss"],
})
export class EmpJobDataDialogComponent extends AppHelpers implements OnInit {
  employeeId: number = 0;
  employeeInstanceId: number = 0;
  @Output() changUpdated = new EventEmitter<boolean>();

  mandatoryColomns: string[] = [
    "Ser",
    // "id",
    "contractType",
    "start_date",
    "end_date",
    "actions",
  ];
  optionalColomns: string[] = [];
  dataSource: MatTableDataSource<IEmpJobDataModel>;

  isLoaded: boolean = false;

  form: FormGroup;
  submitted: boolean = false;

  constructor(
    private employeeJobInformationService: EmployeeJobInformationService
  ) {
    super();
		this.employeeId = EmpGeneralInfoService.EmployeeId;
		this.employeeInstanceId = EmpGeneralInfoService.EmployeeInstanceId;
  }

  ngOnInit(): void {
    this.form = this.fb.group({
      id: [0, { nonNullable: true }],
      status_ID: [null, [Validators.required]],
      statusEventID: [null],
      status_From_Date: [null, [Validators.required]],
      reasons: [null],
      orgTypeId: [null, [Validators.required]],
      orgId: [null, [Validators.required]],
      orgStartDate: [null, [Validators.required]],
      jobId: [null, [Validators.required]],
      startDate: [null, [Validators.required]],
      counted: [null],
      supervisorId: [null, [Validators.required]],
      date: [null, [Validators.required]],
    });
  }

  get formCtrls() {
    return this.form.controls;
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.dataSource?.data?.length > 0)
      this.dataSource = new MatTableDataSource(null);
    if (this.checkNoBusinessOrCompany()) {
      if (this.employeeId > 0) this.getAll();
    }
  }

  getAll() {
    this.employeeJobInformationService
      .getAllByEmployeeId(this.employeeId)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IEmpJobDataModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.dataSource = new MatTableDataSource(res.data);
          this.isLoaded = true;
        } else this.errorList(res.message.messages);
      });
  }

  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    if (this.form.valid) {
      const model: IEmpJobDataRequest = {
        Person_ID: this.employeeId,
        id: this.form.value.id,
        status_ID: this.form.value.status_ID,
        statusEventID: this.form.value.statusEventID,
        status_From_Date: this.form.value.status_From_Date,
        reasons: this.form.value.reasons,
        orgId: this.form.value.orgId,
        orgStartDate: this.form.value.orgStartDate,
        jobId: this.form.value.jobId,
        startDate: this.form.value.startDate,
        counted: this.form.value.counted ?? false,
        supervisorId: this.form.value.supervisorId,
        date: this.form.value.date,
      };

      model.id == 0 ? this.create(model) : this.update(model);
    } // else this.toastr.error("General.formNotValid");
  }

  create(model: IEmpJobDataRequest) {
    this.showLoading();
    this.employeeJobInformationService
      .create(model)
      .subscribe((res: IResponse<IEmpJobDataModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.clearForm();
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
          this.getAll();
          this.changUpdated.emit(true);
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  update(model: IEmpJobDataRequest) {
    this.showLoading();
    this.employeeJobInformationService
      .update(model)
      .subscribe((res: IResponse<IEmpJobDataModel>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.clearForm();
          this.updateSuccessfullyMsg(res?.message?.messageTypeName);
          this.getAll();
          this.changUpdated.emit(true);
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  employeeJobDataToEdit(employeeJobData: IEmpJobDataModel) {
    this.form.patchValue(employeeJobData);
    // console.log(this.form.value);
  }

  onDelete(employeeJobData: IEmpJobDataModel): void {
    let msg = this.msgTranslate(employeeJobData.job);
    this.deleateDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(employeeJobData);
    });
  }

  delete(employeeJobData: IEmpJobDataModel) {
    this.showLoading();
    this.employeeJobInformationService
      .Delete(employeeJobData.id)
      .subscribe((res: IResponse) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
          this.getAll();
          this.changUpdated.emit(true);
          if (employeeJobData.id == this.formCtrls.id.value) this.clearForm();
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  clearForm() {
    this.form.reset({ id: 0 });
    this.submitted = false;
  }
}
