import {Component, ElementRef, HostListener, Inject, OnInit, ViewChild} from "@angular/core";
import {MAT_DIALOG_DATA, MatDialogRef} from "@angular/material/dialog";
import {IEmployee} from "app/personnel-setup/models/employees";
import {EmpGeneralInfoService} from "app/personnel-setup/services/employees/general-info/emp-general-info.service";
import {ICreateDialog} from "app/shared/interfaces/dropdown.interface";
import {AppHelpers} from "app/shared/resources/app-helpers";
import {ViewsManager} from "app/shared/resources/views-manager";

@Component({
  selector: "app-add-employee",
  templateUrl: "./add-employee.component.html",
  styleUrls: ["./add-employee.component.scss"],
})
export class AddEmployeeComponent extends AppHelpers implements OnInit {
  @ViewChild("tabs") tabs: ElementRef;
  employeeId: number = 0; // get from route
  activeTab: number = 0;
  viewsManager = ViewsManager;
  employee: IEmployee;

  constructor(
    @Inject(MAT_DIALOG_DATA) public data: ICreateDialog,
    public dialogRef: MatDialogRef<AddEmployeeComponent>,
    private empGeneralInfoService: EmpGeneralInfoService
  ) {
    super();
    this.activatedRoute.params.subscribe((params) => {
      this.employeeId = Number(params.employeeId ?? 0);
      EmpGeneralInfoService.employeeInstanceId$.next(null);
      EmpGeneralInfoService.employeeId$.next(this.employeeId);
      this.onChangeBusinessAndCompanyId();
    });
  }

  ngOnInit(): void {
    // this.employee = this.empGeneralInfoService.employee$.getValue();
    this.empGeneralInfoService.employee$.subscribe((emp) => {
      this.employee = emp;
    });
  }

  override onChangeBusinessAndCompanyId() {
    this.empGeneralInfoService.employee$.next(null);
    if (this.checkNoBusinessOrCompany() && this.employeeId > 0)
      this.empGeneralInfoService.getEmployeeShared(this.employeeId);
  }

  scrollToSection(event, sectionId: string) {
    event.preventDefault();
    const element = document.getElementById(sectionId);
    if (element) {
      element.scrollIntoView({behavior: "smooth", block: "start"});
    }
  }

  changeEmployeeId(employeeId: number) {
    this.employeeId = employeeId;
  }

  @HostListener("window:scroll", ["$event"])
  onScroll($event) {
    let scrollTop = window.pageYOffset;
    const element = document.getElementById("top-tools");

    // add nav-fixed to tabs
    if (scrollTop >= this.tabs.nativeElement.parentElement.offsetTop + 23) {
      this.tabs.nativeElement.classList.add("nav-fixed");
      element.classList.add("mt-2");
    } else {
      this.tabs.nativeElement.classList.remove("nav-fixed");
      element.classList.remove("mt-2");
    }

    // change active tab
    this.customViews?.forEach((sec, index) => {
      const element = document.getElementById(sec.viewName);
      if (scrollTop >= element.offsetTop) {
        this.activeTab = index;
      }
    });
  }

  onAddNew() {
    this.ngOnDestroy();
  }

  ngOnDestroy(): void {
    this.empGeneralInfoService.cancelRequest$.next();
    this.empGeneralInfoService.cancelRequest$.complete();
    EmpGeneralInfoService.employeeInstanceId$.next(null);
    //  EmpGeneralInfoService.employeeInstanceId$.complete();
    EmpGeneralInfoService.employeeId$.next(null);
    //  EmpGeneralInfoService.employeeId$.complete();
    this.empGeneralInfoService.employee$.next(null);
    //  this.empGeneralInfoService.employee$.complete();
  }
}
