import { Component, OnInit } from "@angular/core";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { EmployeeVacationService } from "app/personnel-setup/services/employees/employee-balances/employee-vacation.service";
import { IResponse } from "app/shared/interfaces/responces";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { EmpVacationDialogComponent } from "../emp-vacation-dialog/emp-vacation-dialog.component";
import { IEmpVacationModel } from "app/personnel-setup/models/employee-balances";
import { EmpGeneralInfoService } from "app/personnel-setup/services/employees/general-info/emp-general-info.service";

@Component({
  selector: "emp-vacation-card",
  templateUrl: "./emp-vacation-card.component.html",
  styleUrls: ["./emp-vacation-card.component.scss"],
})
export class EmpVacationCardComponent extends AppHelpers implements OnInit {
  employeeId: number = 0;
  employeeInstanceId: number = 0;
  employeeVacationInfo: IEmpVacationModel[];

  isLoaded: boolean = false;

  constructor(private employeeVacationService: EmployeeVacationService) {
    super();
		this.employeeId = EmpGeneralInfoService.EmployeeId;
		this.employeeInstanceId = EmpGeneralInfoService.EmployeeInstanceId;
  }

  ngOnInit(): void {}

  override onChangeBusinessAndCompanyId(): void {
    this.employeeVacationInfo = null;
    if (this.employeeId > 0 && this.checkNoBusinessOrCompany()){}
      //this.getAllByEmployeeId();
    else this.isLoaded = true;
  }

  getAllByEmployeeId() {
    this.employeeVacationInfo = null;
    this.employeeVacationService
      .getAllByEmployeeId(this.employeeId)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IEmpVacationModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.employeeVacationInfo = res.data;
          this.isLoaded = true;
        } else this.errorList(res.message.messages);
      });
  }

  openAddEditDialog(): void {
    this.dialogsService
      .addEditDialog(EmpVacationDialogComponent)
      .afterClosed()
      .subscribe((updated: boolean) => {
        if (updated) {
          this.isLoaded = false;
          this.getAllByEmployeeId();
        }
      });
  }
}
